<template>
    <div class="flashSaleCard">
        <van-pull-refresh v-model="isRefresh" @refresh="onRefresh(mgChildKey)">
        <van-list
        v-model="loading"
        :finished="finished"
        @load="onLoad(mgChildKey)"
        :immediate-check="false"
        :style="{'min-height': (listMinHeight+'px')}"
       >
        <div v-for="(item,goodsIndex) in lists" v-if="isLists==false">
            <div :class="themeCurrent == '1' ? 'goodsCard-l':'goodsCard'">
            <!-- 商品图片 -->
            <div :class="themeCurrent == '1' ? 'seckillPic-l' : 'seckillPic'" @click="turnToGoods(item)">
                <img v-lazy="item.goodsPic"/>
                <div class="noStock" v-if="item.spuStatus==3||item.spuStatus==4">补货中</div>
            </div>

            <!-- 商品描述 -->
            <div class="secKillInfo" @click="turnToGoods(item)">
                <div :class="themeCurrent == '1' ? 'secKillName-l' : 'secKillName'">
                    {{item.spuName}}
                </div>
                <div :class="themeCurrent == '1' ? 'salePoint-l' : 'salePoint'">
                    {{item.salePoint}}
                </div>
                <div :style="{'clear':'both'}"></div>
                <div class="salePrice">
                    <div :class="themeCurrent == '1' ? 'secKillPrice-l' : 'secKillPrice'">￥{{item.spuSkPrice}}</div>
                    <div :class="themeCurrent == '1' ? 'normalPrice-l' : 'normalPrice'" v-if="item.originalPrice !=null">￥{{item.originalPrice}}</div>
                </div>
                <van-progress :class="themeCurrent == '1' ? 'saleProgress-l' : 'saleProgress'" :color="'linear-gradient(-135deg, #59b1a3 0%, #17B7BD 100%)'" :show-pivot='false'  :percentage="item.spuStatus==3||item.spuStatus==4?100:item.salesPercent" v-if="mgChildKey=='1'" />
            </div>
            <!-- 秒杀状态 -->
            <div>
                <!-- 开始前状态，大于5分钟 -->
                <div class="secKillStatus" v-if="mgChildKey=='0' && item.time>300000">
                    <!-- <div class="countDownTxt">距开始<van-count-down :style="{'display':'inline-block'}" :auto-start="true" @finish="countDownFinish" :time="item.time" /></div>
                     -->

                     <div class="countDownTxt">距开始<listCountDown :style="{'display':'inline-block'}" :timeBox="item.time">
                </listCountDown>
                     </div>
                    <button :class="themeCurrent == '1' ? 'remindMe-l' : 'remindMe'" @click="remindBefor(item,1)" v-show='item.isSubsribe==0'>提醒我</button>
                    <button :class="themeCurrent == '1' ? 'remindCancle-l' : 'remindCancle'" @click="remindBefor(item,0)" v-show='item.isSubsribe==1' >取消订阅</button>
                </div>
                <!-- 开始前5分钟内 -->
                <div class="secKillStatus" v-if="mgChildKey=='0'&&item.time<=300000">
                    <button class="countDownBtn"><van-count-down  :auto-start="true" :style="{'color':'#FF5050'}" :time="item.time" @finish="countDownFinish" /></button>
                </div>
                <!-- 秒杀进行中,包括补货中状态 -->
                <div class="secKillStatus" v-if="mgChildKey=='1'">
                    <div class="countDownTxt">距结束<van-count-down :style="{'display':'contents'}" :time="item.time" @finish="countDownFinish"/></div>
                    <button :class="themeCurrent == '1' ? 'toBuy-l' : 'toBuy'" v-if='item.spuStatus==3||item.spuStatus==4' :style="{'background':'#999999'}" >已抢空</button>
                    <div  class="addBtn" v-if='!(item.spuStatus==3||item.spuStatus==4)&&item.cartNum>0' @click.stop="addGoods(item,goodsIndex)">
                        <div class="cartNumBox" v-show="item.cartNum!=0" :class="item.cartNum>99?'cartNumBigBox':''">
                            <div class="cartNum">{{item.cartNum}}</div>
                        </div>
                        <img ref="addBtn" src="@/assets/images/mall/mall_icon_add.png" alt="">
                    </div>
                    <button :class="themeCurrent == '1' ? 'toBuy-l' : 'toBuy'" v-if='!(item.spuStatus==3||item.spuStatus==4)&&item.cartNum==0' :style="{'background':'linear-gradient(143deg, #59b1a3 0%, #17B7BD 82%)'}" @click.stop="addGoods(item,goodsIndex)">马上抢</button>
                    <div :class="themeCurrent == '1' ? 'salePercentage-l' : 'salePercentage'">已抢购{{item.spuStatus==3||item.spuStatus==4?100:item.salesPercent}}%</div>
                </div>
            </div>
            <!-- 秒杀结束 -->
                <div class="secKillFinish" v-if="mgChildKey=='2'">
                    <img :src="img.finished"/>
                </div>
        </div>
        </div>
        <!--无列表信息时的显示图片-->
    <div :class="themeCurrent == '1' ? 'noList-l' : 'noList'" v-if="isLists==true">
      <img :src="img.noList"/>
      <div>暂无秒杀</div>
    </div>
    <netWorkError v-if="ifNetWorkError"></netWorkError>
    </van-list>
    </van-pull-refresh>


    <!-- <transition @before-enter="beforeEnter" @enter="enter" @after-enter="afterEnter">
        <img class="circle" v-show="isShowCircle" src="@/assets/images/mall/mall_icon_add.png" alt="">
    </transition> -->
    </div>
</template>
