<script>
import {addGoodsService} from "../../services/goodsService";
import { getFlashSaleList,remindUser } from "../../services/productInfoService";
import { mapActions, mapState } from "vuex";
import Vue from 'vue';
import listCountDown from "../../components/flashSaleCountDown/listCountDown";
import netWorkError from "../../common/networkError/NetworkError"
export default {

    name:'flashSaleCard',
     props:['mgChildKey','isActKey','isClickEven'],
    data:function(){
        return{
            lists:[],
            isLists:false,
            isRefresh:false,//是否下拉刷新
            loading:false,//是否上拉加载
            finished:false,//是否结束上拉加载状态
            currentPage:1,
            totalPage:5,
            pageSize:5,
            ifNetWorkError:false,//判断是否网络异常
            img:{
                finished:require("@/assets/images/mall/image_finish@3x.png"),
                noList:require("@/assets/images/default/no_shop_con_img.png"),//img里面没有找到
            },
            listMinHeight: document.body.clientHeight - this.$stbHeight -65-44,
            // addGoodsLoading:false,
            // isShowCircle:false,
            // addBtnIcon:null,
            themeCurrent: this.$curTheme,
        }
    },
    components:{
    listCountDown, netWorkError
    },
    methods:{

        // // 进入前状态
        // beforeEnter(el){
        //         el.style.left = this.addBtnIcon.getBoundingClientRect().left+'px';
        //         el.style.top = this.addBtnIcon.getBoundingClientRect().top+'px';
        //         el.style.transform = 'scale(1)'
        //     },
        //     // 进入中
        //     enter(el, done){
        //         // el.offsetWidth 强制html渲染动画
        //         // el.offsetWidth 这句话如何不写就不会有动画效果直接渲染的
        //         el.offsetWidth;
        //         el.style.left = 0+'px'
        //         el.style.top =0+'px'
        //         el.style.transform = 'scale(.5)'
        //         done()
        //     },
        //     afterEnter(el){
        //         this.isShowCircle = !this.isShowCircle
        //     },
        // 上拉加载数据
        onLoad(type){
            console.log("mgChildKey"+this.mgChildKey+"isActKey"+this.isActKey);
             if(this.mgChildKey!=this.isActKey){
            return
          }
            console.log(this.currentPage+"下拉刷新");
            this.currentPage+=1;
            if (this.totalPage >= this.currentPage) {
            this.getLists(type);
            this.finished = this.currentPage >= this.totalPage ? true : false;
          } else if (this.totalPage < this.currentPage || this.lists.length==0) {
            console.log("总页数小于等于当前页数了");
            this.finished = true;
            this.loading = false;
            this.isRefresh=false;
          }
        },
        onRefresh(type) {
        this.currentPage = 1;
        this.lists=[];
        // this.finished=false;
        this.getLists(type);
        },
        //清空数据
        emptyData(){
        var arr = [];
        arr.push(this.lists[0])
        this.lists = [];
        },
        // 获取秒杀商品列表
        getLists(type) {
            // const _this=this;
        const data={
            ownerCode: this.$userInfo.OWNER_CODE,
            areaCode:this.$userInfo.AREA_CODE,
            status:type,
            page:this.currentPage,
            rows:10,
            }
          getFlashSaleList(data).then(response =>{
                this.totalPage=response.data.pages;
                this.ifNetWorkError = false;
                this.isLists = response.data.records.length == 0 ? true : false;
                if(response.data.total==0){
                    this.finished=true;
                    this.loading=false;
                    this.isRefresh=false;
                }else{
                    if(this.currentPage==1){
                        this.lists=response.data.records;
                    }else{
                 this.lists=this.lists.concat(response.data.records);
                    }
                    this.lists.map((item,index) => {
                    //图片本地化存储
                    Vue.prototype.$isHasLocalImage(item.goodsPic,function(localImgUrl){
                        item.goodsPic = localImgUrl;
                     })
                });
                // for (var i = 0; i < this.lists.length; i++) {//商品图片切换url

                // if(i%2==0){
                // var img = _this.lists[i].goodsPic;
                // console.log("图片url"+ _this.lists[i].goodsPic);
                // _this.lists[i].goodsPic=img.replace('xwjfile.juhaolian.cn', 'xwjfilea.juhaolian.cn');
                //  console.log("新图片url"+ _this.lists[i].goodsPic);
                //  }
                // }
                this.getCountDown(this.mgChildKey);//获取倒计时
                this.loading = false;
                this.isRefresh=false;
                this.finished= this.currentPage == response.data.pages;
                console.log("是否结束状态"+this.finished)
            }
          }).catch(error => {
          //加载失败后，关闭加载等待的文字
            this.ifNetWorkError = true;
            this.isLists=false;
            this.isRefresh=false;
            this.$toast("网络异常，请稍后重试！");
        });
        },
        // 活动开始前5分钟提醒业主
        remindBefor(item,remind){
            const _this=this;
             if (!_this.isClickEven) {
            return;
          }
            const data={
                startTime:item.startTime,
                // ownerCode:'2019110817314234128',
                ownerCode:this.$userInfo.OWNER_CODE,
                activitySpuId:item.activitySpuId,
                subscribeFlag: remind,
            }
            remindUser(data).then(response =>{
                if(response.resultCode=='0'&&item.isSubsribe==0){
                 _this.$toast("订阅成功");
                 item.isSubsribe = 1;
                }else if(response.resultCode=='0'&&item.isSubsribe==1){
                 _this.$toast("取消订阅成功");
                 item.isSubsribe = 0;
             }else{
               _this.$toast("订阅失败,请稍后重试!");
             }
            });
        },
        //跳转秒杀详情页
        turnToGoods(item){
             if (!this.isClickEven) {
            return;
          }
        this.$router.push({path: '/flashSaleGoods',query:{spuCode:item.spuCode,activitySpuId:item.activitySpuId,goodsUrl:item.goodsPic} });
        },
         /**
         * 添加购物车
         * @param
         * ownerCode Number  业主CODE
         * spuCode   Number  商品CODE
         * skuNo     Number  商品分类CODE
         * shopCode  Number  店铺CODE
         * skuCount  Number  商品数量
         * mark      Number  业主App标识
         */
            addGoods(spuItem,goodsIndex){
            if(spuItem.storeNumber==0){
                this.$toast('该商品正在努力补货中哦')
                return;
            }
            // if(this.addGoodsLoading||this.isShowCircle){
            //     return;
            // }
            // this.addBtnIcon = this.$refs.addBtn[goodsIndex];
            // this.isShowCircle = true;
            // this.addGoodsLoading = true;
            let paramData = {
                ownerCode:this.$userInfo.OWNER_CODE,
                spuCode:spuItem.spuCode,
                skuNo:spuItem.skuNo,
                shopCode:spuItem.shopCode,
                activitySpuId:spuItem.activitySpuId,
                skuCount:1,
                mark:1,
            }
            addGoodsService(paramData).then(res=>{
                if (res.resultCode == "0") {
                    this.$toast({
                        message: '添加购物车成功',
                        duration:1000
                    });
                    this.$emit("getSkuTypeNum");
                    spuItem.cartNum++;
                }else if (res.resultCode == '20100' && res.data.result == '0') {
                    this.$toast('您所添加商品超过最大购买量了哦');
                }
                else{
                    this.$toast('服务器繁忙')
                }
                // this.addGoodsLoading = false;

            }).catch(error=>{
                // this.addGoodsLoading = false;

            })
        },
        // 获取倒计时
        getCountDown(type){
                this.lists.map((item)=>{
                  if(type=='0'){ //未开始状态
                 const secondTime =item.startTime-item.nowTime;
                    item.time =secondTime* 1000;
                  }else if(type=='1'){//秒杀中状态
                const secondTime=item.endTime-item.nowTime;
                item.time= secondTime *1000;
                  }else{
                      item.time=0;
                  }
            });
        },
        countDownFinish(){
            this.onRefresh(this.mgChildKey);
        }
    },
}
</script>
