<template>
  <div>
    <van-count-down :style="{'display':'inline-block'}" :auto-start="true" @finish="countDownFinish" :time="timeBox" />
  </div>
</template>
<style scoped>

</style>
<script>
export default {
    name:"listCountdown",
    props:['timeBox'],
    data:{
        returun:{
        interValTime:null,
        }
    },
    methods:{
         countDownFinish(){
          this.$parent.$parent.$parent.onRefresh(0);
        }
    },
    mounted(){
          let timeDifference=this.timeBox;
        this.interValTime = setInterval(() => {
             timeDifference -= 1000;
            if(timeDifference==300000){
            this.countDownFinish();
          }
        }, 1000);
    },
    destroyed(){
      clearInterval(this.interValTime)
    }
}
</script>
