<script>
      import {getSkuTypeNumService} from "../../services/goodsService";
  import flashSaleCard from './flashSaleCard.vue';
  import NavigationBar from '@/common/currency/NavigationBar.vue';
  export default {
    components: { flashSaleCard, NavigationBar },
    data: function () {
      return {
        tabTitle: ['正在进行', '即将开始'],
        active: 0,
        key: 0,
        firstTab: 0,//首页传值回来的tab状态值
        setTopHeight: {
          'padding-top': this.$stbHeight + 'px'
        },
        adClickFlag: false,
        swipeClickedEven: true,
        skuTypeNum: 0,
        themeCurrent: this.$curTheme,
          goodsListHeight:0,
      }
    },
    filters: {
      setSkuTypeNum(data) {
        return data >= 100 ? "99+" : data
      },
      threshold(data) {
        return data >= 10000 ? ((data + 1000) / 10000).toFixed(1) + '万+' : data
      }
    },
    methods: {
      bottomAdTouchStart: function () {
        // console.log('touch start');
        this.adClickFlag = true;
      },
      bottomAdTouchMove: function () {
        // console.log('touch Move');
        this.adClickFlag = false;
      },
      bottomAdTouchEnd: function (adItem) {
        if (this.adClickFlag) {
          // console.log('点击事件');
          this.swipeClickedEven = true;
        }
        else {
          // console.log('滑动touch事件')
          this.swipeClickedEven = false;
        }
      },
      //       通过滑动和点击切换订单类别
      controlTab(index) {
        const _this = this;
        // _this.active=index;
        _this.key = index;        //同步swipe和tabbar的选中状态
        window.scrollTo(0, 0);//切换swipe时回到页面顶端
      },
      controlSwiper(index) {
        this.active = index;
        this.$nextTick(()=>{
             this.goodsListHeight = this.$refs.flashSaleList.clientHeight-44-50-this.$stbHeight;
            if (index == 0) {
                console.log('===========>>>>>>>>>>>-----------'+index)
                this.$nextTick(()=>{
                    console.log(this.$refs.mychild0)
                })
                this.$refs.mychild0.onRefresh(1);
                // this.$refs.mychild1.emptyData();
                // this.$refs.mychild2.emptyData();
            } else if (index == 1) {
                this.$refs.mychild1.onRefresh(0);
                // this.$refs.mychild0.emptyData();
                // this.$refs.mychild2.emptyData();
            } else {
                this.$refs.mychild1.emptyData();
                this.$refs.mychild0.emptyData();
                // this.$refs.mychild2.onRefresh(2);
            }
        })
      },
      goToBack() {
        if (this.$route.query.flag == 10) {//如果是从支付成功页面返回回来
          this.$route.push({ path: '/productPage' });
        } else {
          this.$router.go(-1);//返回上一层
        }
      },
      countDownFinish() {
        this.onRefresh(this.mgChildKey);
      },
      //设置离开订单列表时的tab
      setTabIndex(flashSaleTabIndex) {
        this.$store.commit("leaveOrderList", {
          flashSaleTabIndex: flashSaleTabIndex,
        })
      },
      /**
       * 查询购物车数量
       * ownerCode  Number  业主CODE
       */
      getSkuTypeNum(isAddGoods = false) {
        let paramData = {
          ownerCode: this.$userInfo.OWNER_CODE
        }
        getSkuTypeNumService(paramData).then(res => {
          if (res.resultCode == 0) {
            // if (isAddGoods) {
            //   Toast({
            //     message: '添加购物车成功',
            //     duration: 1000
            //   });
            // }
            this.skuTypeNum = res.data.skuTypeNum;
          }
        }).catch(error => {
        })
      },
      //去购物车页面
      goToShoppingCart(){
                // this.productScrollTop = this.$refs.scrollContent.scrollTop;
                this.$router.push({
                    name: 'shoppingCart',
                    params: {

                    }
                })
            },
    },
    beforeRouteLeave(to, from, next) {
      if (to.name == 'flashSaleGoods'||to.name == 'shoppingCart'||to.name == 'flashSaleGoodsThemeL') {
        this.setTabIndex(this.active);
      } else {
        this.setTabIndex(5);
      }
      next();
    },
    mounted: function () {

      if (this.$route.params.firstTab == 0 || this.$route.params.firstTab == 1) {//如果是从首页进来
        this.firstTab = this.$route.params.firstTab;
        this.controlSwiper(this.firstTab);
      } else {
        this.firstTab = this.flashSaleTabIndex;
        this.controlSwiper(this.firstTab);
      }
      this.getSkuTypeNum(); //获取购物车数量
    },
    computed: {
      flashSaleTabIndex() {
        return this.$store.state.productPage.flashSaleTabIndex;
      }
    }
  }

</script>
