import http from "../http";


//获取商品详情
const getProductInfo = (reqParam) => {
    return new Promise((resolve, reject) => {
        http.get('xwj-commerce-goods/goods/getGoodsInfoForApp',reqParam).then(result => {
            resolve(result);
        }).catch(error => {
            reject(error);
        })
    });
}
